import React, { Component } from 'react';
import { Row, Col } from 'react-bootstrap';

export default class ListWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {
      list: []
    }
  }

  fetchFirst(url) {
    var that = this;
    if (url) {
      fetch(url).then(response => response.json())
        .then(function (result) {
          that.setState({
            list: result
          });
        });
    }
  }
  componentWillMount() {
    const script = document.createElement("script");
    script.src = "https://files.coinmarketcap.com/static/widget/currency.js";
    script.async = true;
    document.body.appendChild(script);
  }

  render() {
    return (
      <div>
        <Row>
          {/* <Col sm={10}>

          </Col>
          <Col sm={2}>
            <div className="coinmarketcap-currency-widget" data-currencyid="1" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
            <div className="coinmarketcap-currency-widget" data-currencyid="1027" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
            <div className="coinmarketcap-currency-widget" data-currencyid="1376" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
            <div className="coinmarketcap-currency-widget" data-currencyid="2560" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
            <div className="coinmarketcap-currency-widget" data-currencyid="2246" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
            <div className="coinmarketcap-currency-widget" data-currencyid="2554" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div>
          </Col> */}
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="1" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="1027" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="1376" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="2560" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="2246" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
          <Col md={2}><div className="coinmarketcap-currency-widget" data-currencyid="2554" data-base="USD" data-secondary="" data-ticker="true" data-rank="true" data-marketcap="true" data-volume="true" data-stats="USD" data-statsticker="false"></div></Col>
        </Row>
      </div>
    )
  }
}





